import i18n from "i18next";
import { initReactI18next } from "react-i18next";

import en from './resources/en';
import ua from './resources/ua';

i18n
  .use(initReactI18next)
  .init({
    fallbackLng: 'en',
    resources: { en, ua },
    interpolation: {
      escapeValue: false,
    },
  });

export default i18n;
