import React from "react";

import {
  Menu,
  MenuHandler,
  MenuList,
  MenuItem,
  Button,
  Typography,
} from "@material-tailwind/react";

import ScreenContainer from "../../screen-components/ScreenContainer";
import ScreenHeader from "../../screen-components/ScreenHeader";

import { useTranslation } from '../../translations';
import {
  COMMON__ENGLISH,
  COMMON__UKRAINIAN,
  SETTINGS_SCREEN__TITLE,
  SETTINGS_SCREEN__SELECT_LANGUAGE_LABEL,
  SETTINGS_SCREEN__SELECT_LANGUAGE,
} from '../../translations/resources/constants';

const SettingsScreen = () => {
  const { t, handleChangeLanguage } = useTranslation();

  return (
    <ScreenContainer>
      <ScreenHeader title={t(SETTINGS_SCREEN__TITLE)} />

      <div>
        <div className="mb-2">
          <Typography>{t(SETTINGS_SCREEN__SELECT_LANGUAGE_LABEL)}</Typography>
        </div>
        <Menu placement="bottom-end">
          <MenuHandler>
            <Button>{t(SETTINGS_SCREEN__SELECT_LANGUAGE)}</Button>
          </MenuHandler>
          <MenuList>
            <MenuItem onClick={() => handleChangeLanguage('en')}>{t(COMMON__ENGLISH)}</MenuItem>
            <MenuItem onClick={() => handleChangeLanguage('ua')}>{t(COMMON__UKRAINIAN)}</MenuItem>
          </MenuList>
        </Menu>
      </div>

    </ScreenContainer>
  )
}

export default SettingsScreen;
