import React from "react";
import { Link, useLocation } from "react-router-dom";
import {
  Card,
  List,
  ListItem,
} from "@material-tailwind/react";

import Container from '../ui/Container';

import HomeSvg from '../icons/HomeSvg';
import ListSvg from '../icons/ListSvg';
import AddSvg from '../icons/AddSvg';
import TrashSvg from '../icons/TrashSvg';
import SettingsSvg from '../icons/SettingsSvg';

const Sidebar = () => {
  const location = useLocation();

  return (
    <Card className="shadow-xl shadow-blue-gray-900/5">
      <Container>
        <List className="flex flex-row justify-between">
          <Link to="">
            <ListItem selected={location.pathname === '/'}>
              <HomeSvg />
            </ListItem>
          </Link>
          <Link to="list">
            <ListItem selected={location.pathname === '/list'}>
              <ListSvg />
            </ListItem>
          </Link>
          <Link to="add">
            <ListItem selected={location.pathname === '/add'}>
              <AddSvg />
            </ListItem>
          </Link>
          <Link to="trash">
            <ListItem selected={location.pathname === '/trash'}>
              <TrashSvg />
            </ListItem>
          </Link>
          <Link to="settings">
            <ListItem selected={location.pathname === '/settings'}>
              <SettingsSvg />
            </ListItem>
          </Link>
        </List>
      </Container>
    </Card>
  );
}

export default Sidebar;
