import React, { useRef, useState, useEffect } from "react";
import { toLower, prop } from 'ramda'
import {
  Input,
  Button,
  Alert,
} from "@material-tailwind/react";

import { useTranslation } from '../../translations';
import {
  COMMON__FOREIGN,
  COMMON__NATIVE,
  ADD_WORD_SCREEN__TITLE,
  ADD_WORD_SCREEN__DESCRIPTION,
  ADD_WORD_SCREEN__ACTION_ADD,
  ADD_WORD_SCREEN__WORD_ADDED_NOTIFICATION_SUCCESS,
  ADD_WORD_SCREEN__WORD_ADDED_NOTIFICATION_ERROR,
} from '../../translations/resources/constants';
import { useWordPairsDatabase } from '../../database';

import ScreenContainer from "../../screen-components/ScreenContainer";
import ScreenHeader from "../../screen-components/ScreenHeader";

import { getAlertColorByType } from './utils';

const DEFAULT_ALERT = { type: '', message: '' };

const AddScreen = () => {
  const { add } = useWordPairsDatabase();
  const { t } = useTranslation();

  useEffect(() => {
    if (foreignInputRef.current) {
      foreignInputRef.current.focus();
    }
  }, [])

  const [alertData, setAlertData] = useState(DEFAULT_ALERT);

  const foreignInputRef = useRef(null);
  const nativeInputRef = useRef(null);

  const handleOnSubmit = () => {
    if (foreignInputRef.current.value && nativeInputRef.current.value) {
      add({
        foreign: toLower(foreignInputRef.current.value),
        native: toLower(nativeInputRef.current.value),
      })
        .then(() => {
          setAlertData({ type: 'success', message: t(ADD_WORD_SCREEN__WORD_ADDED_NOTIFICATION_SUCCESS) });
        })
        .catch(() => {
          setAlertData({ type: 'error', message: t(ADD_WORD_SCREEN__WORD_ADDED_NOTIFICATION_ERROR) });
        })
        .finally(() => {
          foreignInputRef.current.value = '';
          nativeInputRef.current.value = '';

          foreignInputRef.current.focus();
        })
    }
  }

  return (
    <ScreenContainer>
      <ScreenHeader
        title={t(ADD_WORD_SCREEN__TITLE)}
        description={t(ADD_WORD_SCREEN__DESCRIPTION)}
      />
      <form className="mt-2 mb-2">
        <div className="mb-4 flex flex-col gap-6">
          <Input inputRef={foreignInputRef} size="lg" label={t(COMMON__FOREIGN)} />
          <Input inputRef={nativeInputRef} size="lg" label={t(COMMON__NATIVE)} />
        </div>
        <Button onClick={handleOnSubmit} className="mt-6" fullWidth>
          {t(ADD_WORD_SCREEN__ACTION_ADD)}
        </Button>
      </form>

      <div className="my-4">
        <Alert
          color={getAlertColorByType(prop('type')(alertData))}
          open={Boolean(prop('message')(alertData))}
          variant="ghost"
          onClose={() => setAlertData(DEFAULT_ALERT)}
        >
          {prop('message', alertData)}
        </Alert>
      </div>
    </ScreenContainer>
  )
}

export default AddScreen;
