import React, { useEffect, useState } from "react";
import { map, addIndex, length, prop } from 'ramda';
import {
  IconButton,
  Typography,
} from "@material-tailwind/react";

import { useWordPairsDatabase, useTrashBinDatabase } from '../../database';
import { useTranslation } from '../../translations';
import {
  COMMON__FOREIGN,
  COMMON__NATIVE,
  LIST_SCREEN__TITLE,
  LIST_SCREEN__DESCRIPTION,
} from '../../translations/resources/constants';

import ScreenContainer from "../../screen-components/ScreenContainer";
import ScreenHeader from "../../screen-components/ScreenHeader";
import ScreenSpinner from "../../screen-components/ScreenSpinner";

import EmptyScreen from "../EmptyScreen";

import TrashSvg from '../../icons/TrashSvg';

const ListScreen = () => {
  const { getAll, removeById } = useWordPairsDatabase();
  const { add } = useTrashBinDatabase();
  const { t } = useTranslation();

  const [data, setData] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    getAll()
      .then(setData)
      .finally(() => setIsLoaded(true));
  }, [getAll]);

  const handleOnRemove = (wordPair) => () => {
    Promise.all([
      add({
        foreign: prop('foreign')(wordPair),
        native: prop('native')(wordPair),
      }),
      removeById(prop('id')(wordPair))
        .then(getAll)
        .then(setData)
    ]).catch()
  }

  if (!isLoaded) {
    return <ScreenSpinner />;
  }

  if (!length(data)) {
    return <EmptyScreen />;
  }

  return (
    <ScreenContainer>
      <ScreenHeader
        title={t(LIST_SCREEN__TITLE)}
        description={t(LIST_SCREEN__DESCRIPTION)}
      />
      <table className="w-full table-auto text-left">
        <thead>
          <tr>
            <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-4">
              <Typography
                variant="small"
                color="blue-gray"
                className="font-normal leading-none opacity-70"
              >
                #
              </Typography>
            </th>
            <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-4">
              <Typography
                variant="small"
                color="blue-gray"
                className="font-normal leading-none opacity-70"
              >
                {t(COMMON__FOREIGN)} - {t(COMMON__NATIVE)}
              </Typography>
            </th>
            <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-4" />
          </tr>
        </thead>
        <tbody>
          {addIndex(map)((wordPair, index) => (
            <tr key={prop('id')(wordPair)} className="even:bg-blue-gray-50/50">
              <td className="p-3 border-b border-blue-gray-50">
                {index + 1}
              </td>
              <td className="p-3 border-b border-blue-gray-50">
                <Typography
                  variant="small"
                  color="blue-gray"
                  className="font-normal"
                >
                  {`${prop('foreign')(wordPair)} - ${prop('native')(wordPair)}`}
                </Typography>
              </td>
              <td className="p-3 border-b border-blue-gray-50">
                <div className="flex justify-end">
                  <IconButton variant="text" size="sm" onClick={handleOnRemove(wordPair)}>
                    <TrashSvg />
                  </IconButton>
                </div>
              </td>
            </tr>
          ), data)}
        </tbody>
      </table>
    </ScreenContainer>
  )
}

export default ListScreen;
