import React, { useEffect, useState } from "react";
import { map, addIndex, length, prop } from 'ramda';
import {
  IconButton,
  Typography,
  ButtonGroup,
} from "@material-tailwind/react";

import { useTrashBinDatabase, useWordPairsDatabase } from '../../database';
import { useTranslation } from '../../translations';
import {
  COMMON__NATIVE,
  COMMON__FOREIGN,
  TRASH_BIN_SCREEN__TITLE,
  TRASH_BIN_SCREEN__DESCRIPTION,
} from '../../translations/resources/constants';

import TrashForeverSvg from '../../icons/TrashForeverSvg';
import RefreshSvg from '../../icons/RefreshSvg';

import ScreenContainer from "../../screen-components/ScreenContainer";
import ScreenHeader from "../../screen-components/ScreenHeader";
import ScreenSpinner from "../../screen-components/ScreenSpinner";

import EmptyTrashBinScreen from "../EmptyScreen/EmptyTrashBinScreen";

const TrashBinScreen = () => {
  const { getAll, removeById } = useTrashBinDatabase();
  const { add } = useWordPairsDatabase();
  const { t } = useTranslation();

  const [data, setData] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    getAll()
      .then(setData)
      .finally(() => setIsLoaded(true));
  }, [getAll]);

  const handleOnRemove = (wordPair) => () => {
    removeById(prop('id')(wordPair))
      .then(getAll)
      .then(setData)
      .catch()
  }

  const handleRestore = (wordPair) => () => {
    Promise.all([
      removeById(prop('id')(wordPair))
        .then(getAll)
        .then(setData),
      add({
        foreign: prop('foreign')(wordPair),
        native: prop('native')(wordPair),
      })
    ])
      .catch()
  }

  if (!isLoaded) {
    return <ScreenSpinner />;
  }

  if (!length(data)) {
    return <EmptyTrashBinScreen />;
  }

  return (
    <ScreenContainer>
      <ScreenHeader
        title={t(TRASH_BIN_SCREEN__TITLE)}
        description={t(TRASH_BIN_SCREEN__DESCRIPTION)}
      />

      <table className="w-full min-w-max table-auto text-left">
        <thead>
          <tr>
            <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-4">
              <Typography
                variant="small"
                color="blue-gray"
                className="font-normal leading-none opacity-70"
              >
                #
              </Typography>
            </th>
            <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-4">
              <Typography
                variant="small"
                color="blue-gray"
                className="font-normal leading-none opacity-70"
              >
                {t(COMMON__FOREIGN)} - {t(COMMON__NATIVE)}
              </Typography>
            </th>
            <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-4" />
          </tr>
        </thead>
        <tbody>
          {addIndex(map)((wordPair, index) => (
            <tr key={prop('id')(wordPair)} className="even:bg-blue-gray-50/50">
              <td className="p-4 border-b border-blue-gray-50">
                {index + 1}
              </td>
              <td className="p-4 border-b border-blue-gray-50">
                <Typography
                  variant="small"
                  color="blue-gray"
                  className="font-normal"
                >
                  {`${prop('foreign')(wordPair)} - ${prop('native')(wordPair)}`}
                </Typography>
              </td>
              <td className="p-4 border-b border-blue-gray-50">
                <div className="flex justify-end">
                  <ButtonGroup variant="text">
                    <IconButton variant="text" size="sm" onClick={handleRestore(wordPair)}>
                      <RefreshSvg />
                    </IconButton>
                    <IconButton variant="text" size="sm" onClick={handleOnRemove(wordPair)}>
                      <TrashForeverSvg />
                    </IconButton>
                  </ButtonGroup>
                </div>
              </td>
            </tr>
          ), data)}
        </tbody>
      </table>
    </ScreenContainer>
  )
}

export default TrashBinScreen;
